<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="8">
        <v-card :disabled="loadingForm">
          <div v-if="loadingRecords">
            <v-card-text>
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
              <br />
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
              <br />
              <v-progress-linear indeterminate color="grey"></v-progress-linear>
            </v-card-text>
          </div>
          <div v-else>
            <v-card-subtitle class="d-flex">
              <v-icon class="ml-2 mr-5">mdi-email-outline</v-icon>
              <div>
                <h3 class="font-weight-black">
                  Ticket #{{ id }} - {{ subject }}
                </h3>
                {{ setCreatedAtFormat(createdAt) }} (<i>{{
                  setFromNow(createdAt)
                }}</i
                >)
              </div>
              <v-spacer></v-spacer>
              <router-link :to="{ name: 'MyQueue' }">Back</router-link>
            </v-card-subtitle>

            <v-card-text class="d-flex">
              <v-img
                class="mr-3 rounded"
                :src="require('@/assets/default_selfie.jpg')"
                max-height="40"
                max-width="40"
              ></v-img>
              <div>
                <div class="font-weight-bold">{{ client }}</div>
                <div>{{ email }}</div>
                <div v-show="loanNumber && project && unit && contact">
                  <br />
                  <div>
                    <span class="font-weight-medium">Loan No.:</span>
                    {{ loanNumber }}
                  </div>
                  <div>
                    <span class="font-weight-medium">Project:</span>
                    {{ project }}
                  </div>
                  <div>
                    <span class="font-weight-medium">Unit/Lot No.:</span>
                    {{ unit }}
                  </div>
                  <div>
                    <span class="font-weight-medium">Contact:</span>
                    {{ contact }}
                  </div>
                </div>
              </div>
            </v-card-text>

            <v-card-text class="d-flex">
              <v-icon small class="ml-3 mr-6">mdi-email-outline</v-icon>
              <div class="pre-formatted">
                {{ description }}
              </div>
            </v-card-text>

            <v-card-text>
              <div
                v-for="item in replies"
                :key="item.id"
                class="rounded py-3 mb-3"
                :style="(item.user_id) ? 'background-color: #e6e6e6' : 'border: 1px solid #e6e6e6'"
              >
                <div class="ml-13">
                  <div>
                    <span class="font-weight-bold">{{ item.agent }}</span>
                    replied
                  </div>
                  <i>{{ setFromNow(item.created_at) }}</i>
                </div>
                <br />
                <div class="d-flex">
                  <v-icon small class="ml-3 mr-6">mdi-email-outline</v-icon>
                  <div class="pre-formatted">
                    {{ item.message }}
                  </div>
                </div>
                <div class="ml-13 mt-8" v-show="item.attachments">
                  <v-btn
                    class="text-capitalize"
                    small
                    :color="(item.user_id) ? 'grey lighten-1' : 'grey lighten-4'"
                    @click="download(item.id)"
                    :disabled="downloading"
                  >
                    <v-icon small>mdi-file-download-outline</v-icon>
                    {{ item.attachments ? item.attachments.substring(14) : "" }}
                  </v-btn>
                </div>
              </div>
            </v-card-text>

            <v-card-text v-show="showReply">
              <div
                class="px-3 py-2 rounded-t"
                style="background-color: #1976d2; color: #ffffff"
              >
                <v-icon small dark>mdi-reply-outline</v-icon> To: {{ email }}
              </div>
              <v-row>
                <v-col cols="12">
                  <v-textarea
                    ref="reply"
                    v-model="reply"
                    :rules="[
                      rules.required,
                      rules.minimum(2),
                      rules.maximum(5000),
                    ]"
                    outlined
                    dense
                    rows="8"
                    class="rounded-0"
                    hide-details="auto"
                  ></v-textarea>
                </v-col>
              </v-row>
              <div
                class="px-3 pt-3 pb-2 rounded-b d-flex"
                style="background-color: #1976d2; color: #ffffff"
              >
                <v-btn class="text-capitalize mr-2" small @click="sendReply">
                  <v-icon small>mdi-send-outline</v-icon>
                  Send
                </v-btn>

                <v-file-input
                  ref="attachment"
                  v-model="attachment"
                  label="Attachment"
                  dark
                  dense
                  hide-details="auto"
                  class="mt-0"
                ></v-file-input>

                <v-spacer></v-spacer>
                <v-btn icon small dark @click="showReply = false">
                  <v-icon small>mdi-trash-can-outline</v-icon>
                </v-btn>
              </div>
            </v-card-text>

            <v-card-actions>
              <v-btn class="text-capitalize ml-15" small @click="writeReply">
                <v-icon small>mdi-reply-outline</v-icon>
                Reply
              </v-btn>

              <v-btn
                class="text-capitalize"
                small
                @click="ticketLogsDialog = true"
              >
                <v-icon small>mdi-file-document-outline</v-icon>
                View Logs
              </v-btn>

              <v-btn
                class="text-capitalize"
                small
                @click="solutionsDialog = true"
              >
                <v-icon small>mdi-book-open-page-variant-outline</v-icon>
                &nbsp;Solution
              </v-btn>
            </v-card-actions>
          </div>
        </v-card>
      </v-col>

      <v-col cols="12" md="4">
        <v-form ref="form">
          <v-card :disabled="loadingForm">
            <div>
              <v-card-subtitle class="text-uppercase font-weight-bold">
                Properties <v-divider></v-divider
              ></v-card-subtitle>
              <v-card-text>
                <v-row>
                  <v-col cols="12">
                    <v-autocomplete
                      ref="category"
                      v-model="form.category"
                      :rules="[rules.required]"
                      :items="categories"
                      item-text="name"
                      item-value="id"
                      label="Category"
                      hide-details="auto"
                    ></v-autocomplete>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-select
                      ref="ticketStatus"
                      v-model="form.ticketStatus"
                      :rules="[rules.required]"
                      :items="ticketStatuses"
                      item-text="name"
                      item-value="id"
                      label="Status"
                      hide-details="auto"
                      :disabled="!form.agent"
                      @change="setStatus"
                    ></v-select>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-select
                      ref="priorityStatus"
                      v-model="form.priorityStatus"
                      :rules="[rules.required]"
                      :items="priorityStatuses"
                      item-text="name"
                      item-value="id"
                      label="Priority"
                      hide-details="auto"
                      @change="setPriority(priorityStatuses)"
                    >
                      <template #selection="{ item }">
                        <v-icon
                          small
                          :color="getPriorityColor(item.id)"
                          class="mr-1"
                          >mdi-square</v-icon
                        >
                        {{ item.name }}
                        <span v-if="item.id != 5"
                          >&nbsp;({{ item.hours }} hrs)</span
                        >
                      </template>
                      <template v-slot:item="{ item }"
                        ><v-icon
                          small
                          :color="getPriorityColor(item.id)"
                          class="mr-1"
                          >mdi-square</v-icon
                        >{{ item.name }}
                        <span v-if="item.id != 5"
                          >&nbsp;({{ item.hours }} hrs)</span
                        >
                      </template>
                    </v-select>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      ref="dueDate"
                      v-model="form.dueDate"
                      label="Due date*"
                      :rules="[rules.required, validDueDate]"
                      type="datetime-local"
                      hide-details="auto"
                      :disabled="form.priorityStatus != 5"
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-autocomplete
                      ref="department"
                      v-model="form.department"
                      :rules="[rules.required]"
                      :items="departments"
                      item-text="name"
                      item-value="id"
                      label="Department"
                      hide-details="auto"
                      @change="setAgents"
                    >
                    </v-autocomplete>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-autocomplete
                      ref="agent"
                      v-model="form.agent"
                      :items="agents"
                      item-text="full_name"
                      item-value="id"
                      label="Agent"
                      hide-details="auto"
                      @change="setAgent"
                    ></v-autocomplete>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-btn
                      color="primary"
                      block
                      @click="confirmationDialog = true"
                    >
                      Update
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card-text>
            </div>
          </v-card>
        </v-form>
      </v-col>
    </v-row>

    <solutions
      v-if="solutionsDialog"
      :isAddNew="true"
      @closeSolutionsDialog="closeSolutionsDialog"
    />

    <ticket-logs
      v-if="ticketLogsDialog"
      @closeTicketLogsDialog="closeTicketLogsDialog"
    />

    <confirmation-message
      v-if="confirmationDialog"
      :confirmationData="confirmationData"
      @closeConfirmationDialog="closeConfirmationDialog"
      @onSubmit="onSubmit"
    />
  </v-container>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import { isObjectEmpty } from "@/helpers/form";
import moment from "moment";
import formRules from "@/helpers/formRules";
import TicketLogs from "@/components/ticket/TicketLogs.vue";
import Solutions from "@/components/ticket/Solutions.vue";
import ConfirmationMessage from "@/components/ConfirmationMessage.vue";

export default {
  components: { TicketLogs, Solutions, ConfirmationMessage },

  data: () => ({
    rules: formRules,
    subject: null,
    createdAt: null,
    client: null,
    email: null,
    loanNumber: null,
    project: null,
    unit: null,
    contact: null,
    description: null,
    id: null,
    form: {
      category: null,
      ticketStatus: null,
      priorityStatus: null,
      department: null,
      agent: null,
      dueDate: null,
    },
    solutionsDialog: false,
    isShowSolutionBtn: false,
    ticketLogsDialog: false,
    confirmationDialog: false,
    confirmationData: {
      title: "Are you sure you want to update?",
      text: "This action cannot be undone.",
      closeBtnText: "Cancel",
      closeBtnColor: "gray",
      saveBtnText: "Update",
      saveBtnColor: "primary",
    },
    invalidDueDateText: null,
    showReply: false,
    reply: null,
    attachment: null,
    downloading: false,
  }),

  created() {
    this.getRecord();
  },

  computed: {
    ...mapState("myQueue", [
      "loadingRecords",
      "record",
      "replies",
      "categories",
      "ticketStatuses",
      "priorityStatuses",
      "departments",
      "loadingForm",
      "agents",
      "errors",
    ]),

    ...mapGetters("auth", ["user"]),
  },

  methods: {
    ...mapActions("myQueue", [
      "getAgents",
      "editRecord",
      "updateRecord",
      "doReply",
      "doDownload",
    ]),

    async getRecord() {
      this.$store.commit("myQueue/setRecord", []);
      this.$store.commit("myQueue/setAgents", []);
      await this.editRecord(this.$route.params.id);

      if (Object.keys(this.record).length) {
        this.id = this.record.id;
        this.subject = this.record.subject;
        this.createdAt = this.record.created_at;
        this.client = this.record.client;
        this.email = this.record.email;
        this.loanNumber = this.record.loan_number;
        this.project = this.record.project;
        this.unit = this.record.unit;
        this.contact = this.record.contact;
        this.description = this.record.description;

        this.form.category = this.record.category_id;
        this.form.ticketStatus = this.record.ticket_status_id;
        this.form.priorityStatus = this.record.priority_status_id;
        this.form.dueDate = moment(this.record.due_date).format(
          "YYYY-MM-DDTkk:mm"
        );
        this.form.department = this.record.department_id;
        this.form.agent = this.record.assigned_to;
      }
    },

    setCreatedAtFormat(date) {
      return moment(date).format("MMM D, YYYY h:mm A");
    },

    setFromNow(date) {
      return moment(date).fromNow();
    },

    getPriorityColor(data) {
      switch (data) {
        case 1:
          return "green";
        case 2:
          return "blue";
        case 3:
          return "orange";
        case 4:
          return "red";
        default:
          return "grey";
      }
    },

    setPriority(value) {
      const priority = value.find(
        (item) => item.id === this.form.priorityStatus
      );
      this.form.dueDate = moment(this.createdAt)
        .add(priority.hours, "hours")
        .format("YYYY-MM-DDTkk:mm");

      if (this.form.priorityStatus == 5) {
        this.form.dueDate = null;
      }
    },

    setStatus() {
      if (this.form.ticketStatus == 1) {
        this.form.agent = null;
      }
    },

    setAgents() {
      this.getAgents(this.form.department);
      this.form.agent = null;
      this.form.ticketStatus = 1;
    },

    setAgent() {
      if (this.form.ticketStatus == 1) {
        this.form.ticketStatus = 2;
      }

      if (!this.form.agent) {
        this.form.ticketStatus = 1;
      }
    },

    validDueDate(val) {
      if (val == this.invalidDueDateText) {
        return "Invalid Date";
      } else {
        return true;
      }
    },

    async onSubmit() {
      if (this.$refs.form.validate()) {
        if (!this.loadingForm) {
          this.confirmationDialog = false;
          await this.updateRecord([this.id, this.form]);

          if (!isObjectEmpty(this.errors)) {
            let errorFocus = "";

            Object.entries(this.errors).forEach(([key, value]) => {
              if (value) {
                if (!errorFocus) {
                  errorFocus = key;
                }

                if (key == "dueDate") {
                  this.invalidDueDateText = this.form.dueDate;
                }

                this.$refs.form.validate();
                this.$refs[errorFocus].focus();
              }
            });
          }
        }
      }
    },

    async sendReply() {
      if (this.reply) {
        if (!this.loadingForm) {
          const replyData = {
            ticket_id: this.id,
            message: this.reply,
            attachments: this.attachment,
          };

          await this.doReply(replyData);
          this.showReply = false;
          this.attachment = null;
        }
      } else {
        alert("Reply is required.");
      }
    },

    async download(id) {
      this.downloading = true;
      const downloadData = {
        id: id,
      };
      await this.doDownload(downloadData);
      this.downloading = false;
    },

    closeSolutionsDialog() {
      this.solutionsDialog = false;
    },

    closeTicketLogsDialog() {
      this.ticketLogsDialog = false;
    },

    closeConfirmationDialog() {
      this.confirmationDialog = false;
    },

    writeReply() {
      this.showReply = true;
      this.reply = `Hi ${this.client},

`;
    },
  },

  watch: {
    showReply(visible) {
      if (visible) {
        setTimeout(() => {
          this.$refs.reply.focus();
        }, 200);
      }
    },
  },
};
</script>

<style scoped>
.pre-formatted {
  white-space: pre;
}
</style>